section.client-logos{
    margin-top: 120px;
    margin-bottom: 120px;


    .col-12.col-lg-4 {

        @media (max-width: $bigScreen){
            &{
                width: 43%;
            }
        }

        @media (max-width: $tablet){
            &{
                width: 80%;
                margin-left: auto;
                margin-right: auto;
            }
        }

        @media (max-width: $mobile){
            &{
                width: 100%;
            }
        }
        
    }
    
    .col-12.col-lg-8 {

        @media (max-width: $bigScreen){
            &{
                width: 57%;
            }
        }

        @media (max-width: $tablet){
            &{
                width: 100%;
                margin-top: 35px;
            }
        }
        
    }

    .client-logo-content {
        padding: 67px 80px 67px 60px;
        clip-path: polygon(calc(100% - 100px) 0, 100% 100px, 100% 100%, 0 100%, 0 0);

        @media (max-width: $mobile){
            &{
                padding: 60px 45px 60px 45px;
            }
        }

        h2.heading{
            margin-bottom: 30px;
        }

        .wysiwyg-content{
            margin-bottom: 60px;

            @media (max-width: $bigScreen){
                &{
                    margin-bottom: 35px;
                }
            }
        }

        a{
            color: white;
            text-decoration: none;
            font-size: 18px;
            line-height: 170%;

            > svg{
                margin-left: 18px;
            }
        }
    
        > svg {
            position: absolute;
            bottom: -1px;
            right: 0;
        }
    }

    .client-logos-wrapper {
        margin-bottom: -50px;

        > div {
            padding-bottom: 50px;

            @media (max-width: $tablet){
                &{
                    width: 16.66%;
                }
            }

            @media (max-width: $mobile){
                &{
                    width: 33.33%;
                }
            }

            img{
                @media (max-width: $tablet){
                    &{
                        width: 90%;
                    }
                }
            }
        }

        &:before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            pointer-events: none;
            background: linear-gradient(90deg, white 3%, transparent 30%, transparent 70%, white 97%);
        }
    }
}